
.singer_container{
  font-size: .24rem;
}
.list_group_tit{
  width: 100%;
  text-align: left;
  padding-left: .5rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: .8rem;
  background-color: #efefef;
}
.list_group_item{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 1.4rem;
  width: 100%;
  border-bottom:.02rem solid #efefef;
}
.list_group_item .avatar {
  height: 1rem;
  width: 1rem;
  margin-left: .4rem;
  border-radius: 100%;
}
.list_group_item .name {
  color: #000;
  margin-left: .6rem;
  font-size: .3rem;
}
.list_shortcut{
  position:fixed;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  opacity: .7;
  padding:.4rem 0;
  border-radius: .5rem;
  background-color: #efefef;
  z-index: 5555;
}
.list_shortcut .item {
  line-height: .3rem;
  padding:0 .1rem;
}
.list_shortcut .item.active {
  color: #31c27c;
  font-weight: bold;
}
.list_fixed{
}



