
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  width: 100vw;
  height: 100vh;
  color: #2c3e50;
  /*margin-top: 60px;*/
}
*::-webkit-scrollbar {
  display: none;
}
.header {
  top: 0;
  left: 0;
  position: fixed;
  z-index: 10;
}
.tab {
  position: fixed;
  background: #fff;
  top: 0.88rem;
  z-index: 10;
}
body.modal-open {
    position: fixed;
    width: 100%;
}
.main {
  width: 7.5rem;
  margin: 0 auto;
}



